import shopData from '../mock/shop'
import {
  menu2stoage,
  menuInStoage,
  assignMenu
} from '../util/menu'
export default {
  // 初始化获取本地存储
  initMenu (context) {
    if (!localStorage.getItem('menu')) {
      context.commit('INITMENU', shopData)
    } else {
      let result = assignMenu(shopData.goods[0], JSON.parse(localStorage.getItem('menu')))
      shopData.goods[0] = result
      context.commit('INITMENU', shopData)
    }
  },
  // 加
  addNum ({commit, state}, dispatch) {
    let count = 0
    let num = 0
    shopData.goods[0].children.map((v, i) => {
      if (v.name === dispatch) {
        v.count++
      }
      // 数量
      count += v.count
      // 总计
      num += v.count * v.price
    })
    commit('ADDNUM', { count, num })
    menuInStoage(menu2stoage(shopData.goods, dispatch))
  },
  // 减
  removeNum ({commit, state}, dispatch) {
    let count = 0
    let num = 0
    shopData.goods[0].children.map((v, i) => {
      if (v.name === dispatch) {
        v.count--
      }
      // 数量
      count += v.count
      // 总计
      num += v.count * v.price
    })
    commit('REMOVENUM', { count, num })
    menuInStoage(menu2stoage(shopData.goods, dispatch))
  }
}



