export default {
  ADDNUM (state, list) {
    state.count = list.count
    state.num = list.num
  },
  REMOVENUM (state, list) {
    state.count = list.count
    state.num = list.num
  },
  INITMENU (state, plugins) {
    state.shopData = state.plugins
  }
}



