<template>
  <div id="app">
    <LogoSvg v-if="isShow"></LogoSvg>
    <Animation v-if="isShow"></Animation>
    <router-view v-if="!isShow"/>
  </div>
</template>
<script>
import LogoSvg from './components/logo/logosvg'
import Animation from './components/logo/animation'
export default {
  name: 'App',
  data () {
    return {
      isShow: true
    }
  },
  mounted () {
    setTimeout(() => {
      this.isShow = false
    }, 0)
  },
  components: {
    LogoSvg,
    Animation
  }
}
</script>
<style lang="scss">
@import './assets/style/common.scss';
@import './assets/icon_font/iconfont.css'
</style>



