import Vue from 'vue'
import Vuex from 'vuex'
import MUTATIONS from './mutations'
import ACTIVES from './actives'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count: 0,
    shopData: [],
    num: 0
  },
  mutations: MUTATIONS,
  actions: ACTIVES
})
store.dispatch('initMenu')
export default store



