<template>
  <footer>
    <aside>
      <i class="icon iconfont icon-cart">
        <span>{{ count }}</span>
      </i>
      <b>￥{{num}}</b>
    </aside>
    <button>选好了</button>
  </footer>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'Footer',
  props: ['rightData'],
  computed: {
    ...mapState({
      count: state => state.count,
      num: state => state.num
    })
  }
}
</script>
<style lang="scss" scoped>
footer{
  width:100%;
  height:50px;
  display:flex;
  align-items:center;
  aside{
  width:80%;
  height:50px;
  background:#000;
  line-height:50px;
  position:relative;
  i{
    color:green;
    font-size:30px;
    span{
      display:inline-block;
      width:30px;
      height:30px;
      border-radius:100%;
      background:red;
      color:#fff;
      position:absolute;
      top:4px;
      left:18px;
      font-size:16px;
      text-align:center;
      line-height:30px;
    }
  }
  b{
    color:#fff;
    font-size:18px;
    margin-left:18px;
  }
  }
  button{
    font-size:18px;
    flex:1;
    height:100%;
    padding:2px 4px;
    border:none;
    background:green;
    color:#fff;
  }
}
</style>



