<template>
  <div class="big">
    <div class="appleft">
      <ul>
        <li v-for="(item,n) in contData" :key="n" :class="{'active':currentIndex === n}">
          {{item.name}}
        </li>
      </ul>
    </div>
    <div class="menuwrap" ref="Content">
      <div class="appright">
        <dl v-for="(data,n) in listData" :key="n" ref="MenuLip">
          <dt>
            <img src="../../assets/pic/1.jpg" alt="">
          </dt>
          <dd>
            <h4>{{data.name}}</h4>
            <aside>
              <p><b>￥{{data.price}}</b>/元</p>
              <shopCar :count="data.count" :name="data.name" :price="data.price"></shopCar>
            </aside>
          </dd>
        </dl>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import shopCar from '../../components/shopCar'
export default {
  name: 'App_cont',
  props: [
    'contData',
    'listData'
  ],
  data () {
    return {
      scrollY: 0,
      listHeight: []
    }
  },
  computed: {
    currentIndex () {
      // 求索引值
      for (let i = 0; i < this.listHeight.length; i++) {
        let height = this.listHeight[i]
        // 当前高度
        let height2 = this.listHeight[i + 1]
        // 下一个高度
        if (!height2 || (this.scrollY >= height && this.scrollY < height2)) {
          // 返回索引
          return i
        }
      }
      return 0
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.rightScroll()
      this.getHeight()
    })
  },
  methods: {
    rightScroll () {
      // 设置初始滚动
      this.initScroll = new BScroll(this.$refs.Content, {
        probeType: 3,
        click: true
      })
      // 获取滚动距离
      this.initScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    // 获取每项菜单高度
    getHeight () {
      let MenuLip = this.$refs.MenuLip
      let height = 0
      for (let i = 0; i < MenuLip.length; i++) {
        // 每个dl的内部高
        height += MenuLip[i].clientHeight
        this.listHeight.push(height - 100)
      }
    }
  },
  components: {
    shopCar
  }
}
</script>
<style lang="scss" scoped>
.big{
  width:100%;
  height:100%;
  display:flex;
}
.appleft{
  width:100px;
  height:100%;
  background:#eee;
  font-size:18px;
}
.appleft ul{
  width:100%;
  display:flex;
  flex-direction:column;
}
.appleft ul li{
  width:100%;
  height:60px;
  text-align:center;
  line-height:60px;
  border-bottom:1px solid #ccc;
}
.appleft ul li:first-of-type{
  font-size: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.appright{
  flex: 1;
  padding-left:10px;
  box-sizing:border-box;
  font-size:18px;
}
.appright dl{
  width:100%;
  height:100px;
  display:flex;
  align-items:center;
  border-bottom:1px solid #ccc;
}
.appright dl dt{
  width:60px;
  height:56px;
}
.appright dl dt img{
  width:100%;
  height:100%;
}
.appright dl dd{
  padding-right:10px;
  width:100%;
  flex:1;
  margin-left:10px;
}
.appright dl dd h4{
  font-size:16px;
  width:100%;
  height:30px;
  line-height:30px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.appright dl dd aside{
  width:100%;
  height:40px;
  display:flex;
  justify-content:space-between;
  align-items:center;
}
.appright dl dd aside p{
  color:#ccc;
}
.appright dl dd aside p b{
  color:red;
}
.active{
  background:red;
}
</style>



