let local = window.localStorage
// 遍历找到对应数据
const menu2stoage = (menus, menu) => {
  let obj = {}
  menus[0].children.map((v, i) => {
    if (v.name === menu) {
      obj[v.name] = [v.count, v.price]
    }
  })
  return obj
}
// 把menu放到本地存储
const menuInStoage = (menus) => {
  if (!local.getItem('menu')) {
    local.setItem('menu', JSON.stringify(menus))
    return menus
  } else {
    let result = JSON.parse(local.getItem('menu'))
    Object.assign(result, menus)
    local.setItem('menu', JSON.stringify(result))
    return result
  }
}
// 将接口数据与本地存储数据合并
const assignMenu = (menuList, storageMenu) => {
  menuList.children.forEach((item, key) => {
    // 取数组里的某个字符串是否存在
    if (Object.keys(storageMenu).includes(item.name)) {
      item.count = storageMenu[item.name][0]
      item.price = storageMenu[item.name][1]
    }
  })
  return menuList
}

export {
  menu2stoage,
  menuInStoage,
  assignMenu
}



