<template>
  <div class="app">
    <Head></Head>
    <section>
      <Cont :contData="pageData" :listData="rightData"></Cont>
    </section>
    <Foot :rightData="rightData"></Foot>
  </div>
</template>
<script>
import Head from './menu_wrap/menu_header'
import Cont from './menu_wrap/menu_cont'
import MenuList from '../mock/shop'
import Foot from './menu_wrap/foot'
export default {
  data () {
    return {
      pageData: [],
      rightData: []
    }
  },
  mounted () {
    this.getappData()
  },
  methods: {
    getappData () {
      this.pageData = MenuList.goods
      this.rightData = MenuList.goods[0].children
    }
  },
  components: {
    Head,
    Cont,
    Foot
  }
}
</script>
<style lang="scss">
*{
  padding:0;
  margin:0;
  list-style:none;
}
html,body,#app{
  width:100%;
  height:100%;
}
#app{
  font-size:pxToRem(20px);
}
.app{
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  section{
    width:100%;
    flex:1;
    overflow-y:scroll;
    display:flex;
  }
}
</style>



