<template>
  <div class="nearbyshop">
    <Head></Head>
    <div class="near_cont">
      <ul>
        <router-link to="/mapshop">
          <li>
            <p>
              <span></span>
              <span>中日友好医院</span>
              <span>188米</span>
            </p>
            <p>新世界</p>
            <p>1267126712</p>
          </li>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
import Head from '@/components/near_head'
export default {
  name: 'Nearbyshop',
  components: {
    Head
  }
}
</script>
<style lang="scss">
@import '@/assets/style/common.scss';
*{
  padding:0;
  margin:0;
  list-style:none;
}
.nearbyshop{
  font-size:pxToRem(20px);
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
}
.near_cont{
  flex:1;
  width:100%;
  ul{
    width:100%;
    height:100%;
    li{
      width:100%;
      height:120px;
      padding:0 40px;
      box-sizing:border-box;
      border-bottom:1px solid #ccc;
      p{
        width:100%;
        height:30px;
        display:flex;
        justify-content:space-between;
        align-items:center;
        color:#666;
        &:first-of-type{
        width:100%;
        height:50px;
        display:flex;
        justify-content:space-between;
        align-items:center;
        color:#000;
        }
      }
    }
  }
}
</style>



