<template>
    <div class="car">
      <button v-if="count==0" class="change" @click="add(name)">选规格</button>
      <aside v-else>
        <button @click="del(name)">-</button>
        <span>{{count}}</span>
        <button @click="add(name)">+</button>
      </aside>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      show: true
    }
  },
  props: [
    'count',
    'name',
    'price'
  ],
  methods: {
    ...mapActions({
      removeNum: 'removeNum',
      addNum: 'addNum'
    }),
    del (name) {
      this.removeNum(name)
    },
    add (name) {
      this.addNum(name)
    }
  }
}
</script>
<style lang="scss" scoped>
.change{
  padding:4px 8px;
  color:#fff;
  background:#32CD32;
  border:none;
  border-radius:16px;
}
aside{
    button{
      padding:4px 8px;
      border:none;
      border-radius:16px;
      color:#32CD32;
      background:#fff;
      border:1px solid #999;
    }
    button:last-of-type{
      padding:4px 8px;
      border:none;
      border-radius:16px;
      color:#fff;
      background:#32CD32;
      border:1px solid #999;
    }
  }
</style>



